body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-family:
    Open Sans,
    sans-serif;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 0.5px;
  text-align: left;
  color: #4a4a4a;
  background-color: #f3f7fa;
}

ul {
  list-style-type: none;
  padding: 0;
}
