.container {
  position: relative;
  padding-top: 5px;
}

.ticket {
  max-width: 500px;
  background-color: white;
  padding: 20px;
  margin-bottom: 20px;

  display: flex;
  flex-direction: column;
  gap: 10px;

  border-radius: 7px;

  box-shadow: 0px 0px 15px -5px rgba(34, 60, 80, 0.2);
}

.show-more-btn {
  width: 100%;
  height: 40px;
  border: none;
  border-radius: 7px;
  background-color: #2196f3;
  color: white;

  transition: 0.3s;

  &:hover {
    font-size: 14px;
  }
}

.no-results-message {
  margin-top: 10px;
  border: 1px solid rgba(255, 0, 0, 0.169);
  background-color: rgba(255, 0, 0, 0.031);
  border-radius: 10px;
  font-size: 13px;
  padding: 15px;
  text-align: center;
}

.tickets-list__loader {
  position: absolute;
  top: -5px;
  width: 100%;
  height: 40px;
}
